import React, { Component } from 'react';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';
import './carousel.css';
import axios from 'axios';
import NumberFormat from 'react-number-format';
class Carousel extends Component {

    constructor(props) {
        super(props);
        this.state = {
            carousel: []
        };
    }

    componentDidMount() {
        axios.get('https://192.168.0.101/Project-rest/api/rest/slider').then(res => {
            const carousel = res.data.data;
            // console.log(carousel);
            this.setState({ carousel: carousel });
            // console.log(res.data)
        })
    }

    render() {
        const options = {
            rewind: true,
            autoplay: true,
            lazyLoad: 'nearby',
        }
        return (
            <div className="carousel" >
                <Splide options={options}>
                    {this.state.carousel.map((items) => {
                        return (
                            <SplideSlide >
                                <div className="slide__content">
                                    <div className="content__left">
                                        <div className="title__product">
                                            <h1>{items.nama}</h1>
                                        </div>
                                        <div className="price__product">
                                            <NumberFormat value={items.price} displayType={'text'} thousandSeparator={true} prefix={'Rp '} renderText={value => <h4 className="final__price"> {value}</h4>} />
                                            {/* <h4 className="diskon__price">{items.diskon}</h4> */}
                                        </div>
                                        <span>
                                            Check now
                                        </span>
                                        <div className="text__footer">
                                            <p>Shipping</p>
                                            <h5>Worldwide</h5>
                                        </div>
                                    </div>
                                    <div className="content__right">
                                        <img src={items.thumb_img} alt="carousel" />
                                    </div>
                                </div>
                            </SplideSlide>
                        )
                    })}
                </Splide>
            </div>
        );
    }
}

export default Carousel;
