import React from 'react';
import './slider-item.css';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';

import NumberFormat from 'react-number-format';
import TextTruncate from 'react-text-truncate';

import {
    Link
} from "react-router-dom";

function SliderItems(props) {

    const options = {
        type: 'loop',
        perPage: 1,
        perMove: 1,
        autoWidth: true,
        pagination: false,
        autoplay: true,
        // lazyLoad: 'nearby',
    }

    return (
        <div className="SliderItems">
            <div className="slider__top">
                <div className="title__top">
                    {props.title}
                </div>
            </div>
            <div className="slider__content">
                <Splide options={options}>
                    {props.product.map((items) => {
                        return (
                            <SplideSlide>
                                <Link to={`${items.id}`}>
                                    <div className="card__cat">
                                        <div className="image__card">
                                            <img src={items.thumb_img} alt="items" />
                                        </div>
                                        <div className="card__footer">
                                            <h4>
                                                <TextTruncate
                                                    line={1}
                                                    element="span"
                                                    truncateText="â€¦"
                                                    text={items.nama}
                                                />
                                            </h4>
                                            <div>
                                                <NumberFormat value={items.price} displayType={'text'} thousandSeparator={true} prefix={'Rp '} renderText={value => <p className="price"> {value} </p>} />
                                                {/* <span className="btn-purchase">Check Now</span> */}
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                            </SplideSlide>
                        )
                    })}
                </Splide>
            </div>
        </div>
    );
}

export default SliderItems;
