import React, { Component } from 'react';
import './detail-product.scss'

import { Splide, SplideSlide } from '@splidejs/react-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';

import Magnifier from "react-magnifier";

import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import axios from 'axios';
import NumberFormat from 'react-number-format';
import ReactHtmlParser from 'react-html-parser';

import Iklan from '../img/68747470733a2f2f692e696d6775722e636f6d2f544d53744238582e706e67.png'

class DetailProduct extends Component {

    constructor(props) {
        super(props);
        this.state = {
            image: '',
            detail: [],
            seller: [],
        };
        this.id = props.id;
    }

    componentDidMount() {
        axios.get('https://192.168.0.101/Project-rest/api/rest/product_detail?id=' + this.id).then(res => {
            const product = res.data.data;
            // console.log(product[0].base + 'thumb-img/' + product[0].product[0].thumb_image)
            this.setState({ detail: product });

            var ImageTarget = document.querySelector('.image');
            ImageTarget.src = this.setState({ image: product[0].base + 'thumb-img/' + product[0].product[0].thumb_image });

            axios.get('https://192.168.0.101/Project-rest/api/rest/product_detail?id=' + product[0].product[0].seller).then(resp => {
                const sell = resp.data;

                this.setState({ seller: sell })

            })

        })
    }

    componentDidUpdate() {
        var ImageTarget = document.querySelector('.image');
        ImageTarget.src = this.state.image;
    }

    render() {
        const options = {
            perPage: 2,
            perMove: 1,
            autoWidth: true,
            pagination: false,
            lazyLoad: 'nearby',
        }

        return (
            < div className="DetailProduct" >
                {this.state.detail.map((items) => {
                    return (
                        <div>
                            <div className="detail__container" >
                                <div className="detail__left">
                                    <div className="detail__image">
                                        <Magnifier className="image" src={this.state.image} width={'100%'} />
                                    </div>
                                    <div className="detail__carausel">
                                        <Splide options={options}>
                                            {items.product.map((thumb) => {
                                                return (
                                                    <SplideSlide>
                                                        <img onClick={() => this.setState({ image: items.base + 'thumb-img/' + thumb.thumb_image })} className="detail__card image-data" src={items.base + 'thumb-img/' + thumb.thumb_image} alt="carousel" />
                                                    </SplideSlide>
                                                )
                                            })}
                                            {items.image.map((img) => {
                                                return (
                                                    <SplideSlide>
                                                        <img onClick={() => this.setState({ image: items.base + '/upload/' + img.path + '/' + img.image })} className="detail__card image-data" src={items.base + '/upload/' + img.path + '/' + img.image} alt="carousel" />
                                                    </SplideSlide>
                                                )
                                            })}
                                        </Splide>
                                    </div>
                                </div>

                                {items.product.map((product) => {
                                    return (
                                        <div className="detail__right">
                                            <div className="detail__name-product">
                                                {product.prod_nama}
                                            </div>
                                            <hr />
                                            <div className="detail__content">
                                                <div className="detail__items-title">
                                                    Harga
                                        </div>
                                                <div className="detail__container-price">
                                                    {/* <div className="detail__diskon">
                                                        <div className="diskon__badge-price">
                                                            50%
                                                </div>
                                                        <div className="diskon-price">
                                                            Rp500.000
                                                </div>
                                                    </div> */}
                                                    <NumberFormat value={product.prod_price} displayType={'text'} thousandSeparator={true} prefix={'Rp '} renderText={value => <div className="detail__price-final"> {value}</div>} />
                                                </div>
                                            </div>
                                            <hr />
                                            <div className="detail__content">
                                                <div className="detail__items-title">
                                                    Info Product
                                                </div>
                                                <div className="detail__info-product">
                                                    <div className="product-items">
                                                        <div className="items-title">Berat</div>
                                                        <div className="items-info">{product.prod_weight + 'gr'}</div>
                                                    </div>
                                                    <div className="product-items">
                                                        <div className="items-title">Kondisi</div>
                                                        <div className="items-info">{product.kondisi}</div>
                                                    </div>
                                                    <div className="product-items">
                                                        <div className="items-title">Stock</div>
                                                        <div className="items-info">{product.stock + ' ' + product.prod_unit}</div>
                                                    </div>
                                                    <div className="product-items">
                                                        <div className="items-title">Merk</div>
                                                        <div className="items-info">{product.merk}</div>
                                                    </div>
                                                </div>
                                            </div>
                                            <hr />
                                            <div className="detail__content">
                                                <div className="detail__items-title">
                                                    Varian Product
                                                </div>
                                                <div className="detail__info-varian">
                                                    {items.varian.map((varian) => {
                                                        if (varian) {
                                                            return (
                                                                <div className="detail__info-varian__items">
                                                                    {varian.varian}
                                                                </div>
                                                            )
                                                        }
                                                    })}
                                                </div>
                                            </div>
                                            <hr />
                                            {items.seller.map((sell) => {
                                                return (
                                                    <div>
                                                        <div className="detail__content">
                                                            <div className="seller-profile">
                                                                <div className="avatar-seller">
                                                                    <img src={sell.avatar} alt="avatar profile" />
                                                                </div>
                                                                <div className="seller-info">
                                                                    <h1>{sell.shop}</h1>
                                                                    <span>{sell.name}</span>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="detail__content">
                                                            <div className="detail__content-buy">
                                                                <a href={`https://web.whatsapp.com/send?phone=+6289517214711&text=https://bocahgabut.github.io/market-me/ %0a Apakah Product Ini Masih Tersedia ?`} target="_blank" className="btn-buynow" rel="noopener noreferrer">
                                                                    <WhatsAppIcon />
                                                                        Buy Now on Whatsapp
                                                                </a>
                                                            </div>
                                                        </div>
                                                    </div>
                                                )
                                            })}
                                            {/* <div className="detail__content">
                                                <div className="detail__items-title">
                                                    Ongkos Kirim
                                                </div>
                                                <div className="check__alamat">
                                                    <div>
                                                        <div className="alamat-title">
                                                            Ke
                                                        </div>
                                                        <div className="dropdown-alamat">
                                                            <div className="dropdown-select">
                                                                <span className="select-title">
                                                                    Kota Cirebon, Lemahwungkuk</span>
                                                                <ExpandMoreIcon className="select-icons" />
                                                            </div>
                                                            <div className="dropdown__list">
                                                                <div className="dropdown__list-items">Pilih alamat</div>
                                                                <div className="dropdown__list-items">Kode pos</div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="price-ongkir">
                                                        Rp 9.000
                                                    </div>
                                                </div>
                                            </div>
                                        */}
                                        </div>
                                    )
                                })}
                            </div>
                            <div className="iklan__container">
                                <div className="iklan">
                                    <img src={Iklan} alt="iklan" />
                                </div>
                                <div className="iklan">
                                    <img src={Iklan} alt="iklan" />
                                </div>
                            </div>
                            {items.product.map((product) => {
                                return (
                                    <div className="detail__description">
                                        <div className="description-card">
                                            <div className="card-header">
                                                <h3>Deskripsi Product</h3>
                                            </div>
                                            <hr />
                                            <div className="card-items">
                                                {ReactHtmlParser(product.description)}
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    )
                })
                }
            </div >
        );
    }
}

export default DetailProduct;
