import React, { Component } from 'react';

import Loader from '../component/loader/index.jsx';
import Footer from '../component/footer.jsx';
import NavbarTop from '../component/navbar-top.jsx';
import DetailProduct from '../component/detail-product.jsx';
import Slider from '../component/slider-item.jsx';

import axios from 'axios';

class DetailPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            more: []
        }
        this.detail = props.match.params.detail;
    }

    componentDidMount() {
        axios.get('http://192.168.0.101/Project-rest/api/rest/product_detail?id=' + this.detail).then(res => {
            const product = res.data.data;

            axios.get('http://192.168.0.101/Project-rest/api/rest/more?id=' + product[0].product[0].seller).then(resp => {
                const mores = resp.data.data;
                console.log(mores)
                this.setState({ more: mores })

            })

        })
    }

    render() {
        return (
            <div className="DetailPage" >
                <Loader />
                < NavbarTop />
                <DetailProduct
                    id={this.detail}
                />
                <Slider
                    title="MORE PRODUCT"
                    product={this.state.more}
                />
                <Footer />
            </div>
        );
    }
}

export default DetailPage;
