import React from 'react';
import './footer.css';

import QueryBuilderIcon from '@material-ui/icons/QueryBuilder';
import SecurityIcon from '@material-ui/icons/Security';
import RecordVoiceOverIcon from '@material-ui/icons/RecordVoiceOver';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';

import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import InstagramIcon from '@material-ui/icons/Instagram';
import SendIcon from '@material-ui/icons/Send';

function Footer() {
    return (
        <div className="Footer">
            <div className="main__content">
                <div className="left box">
                    <h2>About us</h2>
                    <div className="content">
                        <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Rerum inventore dolorem sunt fugit esse ad modi soluta voluptas alias sint. Consectetur commodi tempora iure magni amet totam itaque odit reiciendis hic, quae natus delectus tenetur.</p>
                        <div className="social__media">
                            <span className="fb">
                                <FacebookIcon />
                            </span>
                            <span className="twt">
                                <TwitterIcon />
                            </span>
                            <span className="ig">
                                <InstagramIcon />
                            </span>
                        </div>
                    </div>
                </div>
                <div className="center box">
                    <h2>Services</h2>
                    <div className="content">
                        <div className="items">
                            <div className="icons__">
                                <QueryBuilderIcon />
                            </div>
                            <h4>Fast Respon</h4>
                        </div>
                        <div className="items">
                            <div className="icons__">
                                <SecurityIcon />
                            </div>
                            <h4>Secure</h4>
                        </div>
                        <div className="items">
                            <div className="icons__">
                                <RecordVoiceOverIcon />
                            </div>
                            <h4>Trusted</h4>
                        </div>
                        <div className="items">
                            <div className="icons__">
                                <ThumbUpIcon />
                            </div>
                            <h4>Friendly</h4>
                        </div>
                    </div>
                </div>
                <div className="center__list box">
                    <h2>Quick links</h2>
                    <ul className="content">
                        <li>Home</li>
                        <li>Products</li>
                        <li>Blog</li>
                        <li>Check Resi</li>
                        <li>About us</li>
                    </ul>
                </div>
                <div className="right box">
                    <h2>Contact us</h2>
                    <div className="content">
                        <form action="#">
                            <div className="form__group">
                                <div className="label">
                                    Email *
                               </div>
                                <input type="email" />
                            </div>
                            <div className="form__group">
                                <div className="label">
                                    Message
                               </div>
                                <textarea rows="3"></textarea>
                            </div>
                            <button type="submit" className="btn__submit">Send<SendIcon className="icon-send" /></button>
                        </form>
                    </div>
                </div>

            </div>
            <hr />
            <div className="text__copyright">
                <span>Copyright &#169; 2020 All Rights Reserved <span>Boch_gabut</span> &#8482;</span>
                <span>Created By : Restu Maulana</span>
            </div>
        </div>
    );
}

export default Footer;
