import React, { Component } from 'react';
import './App.css';
// import HomePage from './page/home-page.jsx'
import ProductPage from './page/product-page.jsx'
import ResiPage from './page/resi-page.jsx'

import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import DetailPage from './page/detail-page.jsx';

class App extends Component {

  render() {

    return (
      <Router >
        <div className="App">
          <Switch>
            <Route exact path="/market-me/">
              {/* <HomePage /> */}
              <ProductPage />
            </Route>
            <Route exact path="/market-me/product/">
              <ProductPage />
            </Route>
            <Route exact path="/market-me/product/detail/:detail" component={DetailPage} />
            <Route exact path="/market-me/check-resi">
              <ResiPage />
            </Route>
          </Switch>
        </div>
      </Router>
    )
  }
}

export default App;
