import React, { Component } from 'react';
import './loader.css';

class Loader extends Component {

    componentDidMount() {
        const content_loader = document.querySelector('.main__loader'),
            content_mount = document.querySelector('*')

        var showPage = () => {
            content_loader.style.display = "none";
            content_loader.classList.remove('.main__loader');
            content_loader.classList.add('.hide__content');
            content_mount.classList.remove('.hide__content');
            // content_mount.style.overflow = "hidden";
            // content_mount.style.overflow = "auto";
        }

        const loading = () => {
            if (content_loader) {
                setTimeout(showPage, 2000);
            }
        }
        loading()
    }

    render() {

        return (
            <div className="main__loader">
                <div className="content__loader">
                    <div className="loader">
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                </div>
            </div>
        )
    }
}

export default Loader;
