import React, { Component } from 'react';
import './navbar-top.css';
import Logo from '../img/logo.png';

import {
    Link,
    NavLink
} from "react-router-dom";

class NavbarTop extends Component {

    componentDidMount() {
        const showMenu = (toggleId, navId) => {
            const nav = document.querySelector(navId),
                toggle = document.querySelector(toggleId),
                hamb = document.querySelector('.hamburger')

            if (toggle && nav) {
                toggle.addEventListener('click', function () {
                    nav.classList.toggle('show')
                    hamb.classList.toggle('is-active')
                })
            }

        }
        showMenu('.menu__toggle', '.nav__links')
    }

    render() {
        return (
            <div className="navbar__top" >
                <div className="logo__container">
                    <img src={Logo} alt="Logo" />
                </div>
                <div className="nav__links">
                    {/* <div className="nav__items">
                        <Link to="/market-me/">Home</Link>
                    </div> */}
                    <NavLink to="/market-me/product/" className="nav__items" activeClassName="active" >Product</NavLink>
                    {/* <div className="nav__items ">
                        <Link to="/market-me/">Product</Link>
                    </div> */}
                    <NavLink to="/market-me/blog/" className="nav__items" activeClassName="active" >Blog</NavLink>
                    <NavLink to="/market-me/check-resi" className="nav__items" activeClassName="active" >Check Resi</NavLink>
                    <NavLink to="/market-me/about-us" className="nav__items" activeClassName="active" >About Us</NavLink>

                </div>
                <div className="menu__toggle" >
                    <div class="hamburger" id="hamburger-11">
                        <span class="line"></span>
                        <span class="line"></span>
                        <span class="line"></span>
                    </div>
                </div>
            </div>
        );
    }
}

export default NavbarTop;
