import React, { Component } from 'react';
import './product.scss'
import Grade from 'grade-js'

import axios from 'axios';
import NumberFormat from 'react-number-format';
import TextTruncate from 'react-text-truncate';

import {
    Link
} from "react-router-dom";

class Product extends Component {

    constructor(props) {
        super(props);
        this.state = {
            product: []
        };
    }

    componentDidMount() {
        window.addEventListener('load', function () {
            Grade(document.querySelectorAll('.gradient-wrap'))
        });

        axios.get('https://192.168.0.101/Project-rest/api/rest/product').then(res => {
            const products = res.data.data;
            // console.log(products);
            this.setState({ product: products });
            // console.log(res.data)
        })
    }

    render() {
        return (
            <div className="Product" >
                {/* <div class="search__container">
                    <p class="search__title">
                        You can search Something
                    </p>
                    <input class="search__input" type="text" placeholder="Type..." />
                </div> */}
                <div className="prod__top">
                    <div className="sub__prod">Happy</div>
                    <div className="title__prod">Shopping</div>
                </div>
                <div className="container__product">
                    {this.state.product.map((items) => {
                        return (
                            <Link to={`detail/${items.id}`}>
                                <div className="product__card">
                                    <div className="product__image">
                                        <img src={items.thumb_img} alt="product" />
                                    </div>
                                    <div className="product__detail">
                                        <div className="product__name">
                                            <TextTruncate
                                                line={1}
                                                element="span"
                                                truncateText="â€¦"
                                                text={items.nama}
                                            />
                                        </div>
                                        <NumberFormat value={items.price} displayType={'text'} thousandSeparator={true} prefix={'Rp '} renderText={value => <div className="product__price"> {value} </div>} />
                                    </div>
                                </div>
                            </Link>
                        )
                    })}
                </div>
            </div>
        );
    }
}

export default Product;
